import React from "react";

const EditAccountModal = (props) => {
  if (!props.showEdit) {
    return null;
  }

  return (
    <div className="modal-edit-account" onClick={props.onClose}>
      <div className="cart-update" onClick={(e) => e.stopPropagation()}>
        <form className="form-create">
          <h3>Chnh sa hoc xo ti khon</h3>

          <div className="form-input">
            <label htmlFor="avt">nh</label>
            <input type="file" id="avt" placeholder="Hy chn nh" />
          </div>

          <div className="form-input">
            <label htmlFor="mssv">M s sinh vin</label>
            <input
              type="text"
              id="mssv"
              placeholder="Hy nhp m s sinh vin"
              defaultValue="PS16509"
            />
          </div>

          <div className="form-input">
            <label htmlFor="fullname">H v tn</label>
            <input
              type="text"
              id="fullname"
              placeholder="Hy nhp h v tn"
              defaultValue="Trn Mu Phi"
            />
          </div>

          <div className="form-input">
            <label htmlFor="email">Email</label>
            <input
              type="email"
              id="email"
              placeholder="Hy nhp email"
              defaultValue="phitmps16509@fpt.edu.vn"
            />
          </div>
          <div className="buttons-update">
            <button>
              <i className="uil uil-plus"></i> Cp nht
            </button>
            <button>
              <i className="uil uil-trash"></i> Xo
            </button>
            <button>
              <i className="uil uil-redo"></i> Lm mi
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default EditAccountModal;
