import React from "react";
import { Link } from "react-router-dom";
import Nav from "../../general/Nav/index";
import useLogin from "../../../utils/useLogin/useLogin";

export default function ChangePass() {
  const { account } = useLogin();

  return (
    <React.Fragment>
      <Nav />
      <main>
        <div className="container-profile">
          <div className="profile">
            <div className="title">
              <img src={account.avatar} alt="" />
            </div>
            <div className="info">
              <p>{account.fullName}</p>
              <span className="text-muted">{account.email}</span>
              <br />
              <span className="text-muted">Sinh vin FPT Polytechnic</span>
            </div>
            <div className="link">
              <Link to={"/profile"}>Thng tin</Link>
            </div>
            <div className="link">
              <Link to={"/profileDetail"}>Chnh sa</Link>
            </div>
            <div className="link">
              <Link to={"/changePass"}>i mt khu</Link>
            </div>
          </div>
          <div className="profile-details">
            <div className="title">
              <span>Ti khon ca ti</span>
            </div>
            <form action="/profile">
              <div className="profile-details-body">
                <h6>Thng tin i mt khu</h6>
                <div className="profile-details-row">
                  <div className="form-input">
                    <label htmlFor="password">Mt khu</label>
                    <input
                      type="password"
                      id="password"
                      placeholder="Hy nhp mt khu ca bn."
                      defaultValue="123123123123"
                    />
                  </div>

                  <div className="form-input">
                    <label htmlFor="re-password">Nhp li mt khu</label>
                    <input
                      type="password"
                      id="re-password"
                      placeholder="Hy nhp nhp li mt khu ca bn."
                      defaultValue="123123123123"
                    />
                  </div>

                  <div className="form-input">
                    <label htmlFor="new-password">Mt khu mi</label>
                    <input
                      type="password"
                      id="new-password"
                      placeholder="Hy nhp mt khu mi ca bn."
                      defaultValue="123123123123"
                    />
                  </div>

                  <div className="form-input">
                    <label htmlFor="confirm-password">
                      Xc nhn mt khu mi
                    </label>
                    <input
                      type="password"
                      id="onfirm-password"
                      placeholder="Hy nhp mt khu ca bn."
                      defaultValue="123123123123"
                    />
                  </div>
                </div>

                <button type="submit">i mt khu</button>
              </div>
            </form>
          </div>
        </div>
      </main>
    </React.Fragment>
  );
}
