import React from "react";
import { Link } from "react-router-dom";
import Nav from "../../general/Nav/index";
import useLogin from "../../../utils/useLogin/useLogin";

export default function Profile() {
  const { account } = useLogin();

  return (
    <React.Fragment>
      <Nav />
      <main>
        <div className="container-profile">
          <div className="profile">
            <div className="title">
              <img src={account.avatar} alt="" />
            </div>
            <div className="info">
              <p>{account.fullName}</p>
              <span className="text-muted">{account.email}</span>
              <br />
              <span className="text-muted">Sinh vin FPT Polytechnic</span>
            </div>
            <div className="link">
              <Link to={"/profile"}>Thng tin</Link>
            </div>
            <div className="link">
              <Link to={"/profileDetail"}>Chnh sa</Link>
            </div>
            <div className="link">
              <Link to={"/changePass"}>i mt khu</Link>
            </div>
          </div>
          <div className="profile-details">
            <div className="title">
              <span>Ti khon ca ti</span>
            </div>
            <form action="/profile">
              <div className="profile-details-body">
                <h6>Thng tin ngi dng</h6>
                <div className="profile-details-row">
                  <div className="form-input">
                    <label htmlFor="avatar">nh</label>
                    <input
                      type="file"
                      id="avatar"
                      placeholder="Hy chn nh ca bn."
                    />
                  </div>

                  <div className="form-input">
                    <label htmlFor="stCode">M s sinh vin</label>
                    <input
                      type="text"
                      id="stCode"
                      placeholder="Hy nhp m s sinh vin ca bn."
                      defaultValue="PS16509"
                    />
                  </div>

                  <div className="form-input">
                    <label htmlFor="name">H v tn</label>
                    <input
                      type="text"
                      id="name"
                      placeholder="Hy nhp h v tn ca bn."
                      defaultValue="Trn Mu Phi"
                    />
                  </div>

                  <div className="form-input">
                    <label htmlFor="email">Email</label>
                    <input
                      type="email"
                      id="email"
                      placeholder="Hy nhp email ca bn."
                      defaultValue="phitmps16509@fpt.edu.vn"
                    />
                  </div>
                </div>

                <div className="hr"></div>

                <h6>Thng tin lin lc</h6>
                <div className="profile-details-row">
                  <div className="form-input">
                    <label htmlFor="birthday">Ngy sinh</label>
                    <input
                      type="date"
                      id="birthday"
                      defaultValue="2002-04-26"
                    />
                  </div>

                  <div className="form-input">
                    <label htmlFor="gender">Gii tnh</label>
                    <div className="gender">
                      <div>
                        <input
                          type="radio"
                          name="gender"
                          id="gender"
                          defaultValue="true"
                        />
                        <span className="checkmark">Nam</span>
                      </div>
                      <div>
                        <input
                          type="radio"
                          name="gender"
                          id="gender"
                          defaultValue="false"
                        />
                        <span className="checkmark">N</span>
                      </div>
                    </div>
                  </div>

                  <div className="form-input">
                    <label htmlFor="major">Chuyn ngnh</label>
                    <div className="select-box ">
                      <select className="select">
                        <option defaultValue="" defaultChecked>
                          Hy chn chuyn ngnh
                        </option>
                        <option defaultValue="1">ng dng phn mm</option>
                        <option defaultValue="2">Pht trin phn mm</option>
                        <option defaultValue="3">Thit k website</option>
                        <option defaultValue="4">Lp trnh mobile</option>
                        <option defaultValue="5">Thit k  ho</option>
                      </select>
                    </div>
                  </div>

                  <div className="form-input">
                    <label htmlFor="address">a ch</label>
                    <input
                      type="text"
                      id="address"
                      placeholder="Hy nhp a ch ca bn."
                      defaultValue="Qun 12, TP.H Ch Minh"
                    />
                  </div>

                  <div className="form-input">
                    <label htmlFor="major">Kho</label>
                    <input
                      type="text"
                      id="major"
                      placeholder="Hy nhp kho ca bn."
                      defaultValue="16.3"
                    />
                  </div>

                  <div className="form-input">
                    <label htmlFor="dateCreate">Ngy to</label>
                    <input
                      type="date"
                      id="dateCreate"
                      placeholder="Hy nhp ngy to ca bn."
                      defaultValue="2022-10-01"
                    />
                  </div>
                </div>
                <button type="submit">Cp nht</button>
              </div>
            </form>
          </div>
        </div>
      </main>
    </React.Fragment>
  );
}
