import React, { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import Asios from "./../../../../api/index";
import {
  UilHome,
  UilUsersAlt,
  UilFeedback,
  UilFileUpload,
  UilSignout,
  UilEllipsisH,
  UilLock,
  UilUserCheck,
  UilPlus,
} from "@iconscout/react-unicons";
import Nav from "../../general/Nav/index";

import CreateModalQuiz from "./CreateModalQuiz";
import Quiz from "../../general/Quiz";

export default function PageQuiz() {
  const [show, setShow] = useState(false);
  const [group, setGroup] = useState([]);
  const { id } = useParams();

  useEffect(() => {
    getAllData();
  }, []);

  const getAllData = async () => {
    const response = await Asios.Groups.getOneGroup(id);
    setGroup(response);
  };

  return (
    <React.Fragment>
      <Nav />
      <main className="pages">
        <div className="container-page">
          <div className="left">
            <div className="background">
              <img
                src="https://www.facebook.com/images/groups/groups-default-cover-photo-2x.png"
                alt=""
              />
            </div>
            <div className="title">
              <span>{group.name}</span>
              <p className="text-muted">
                <i>
                  <UilLock />
                </i>{" "}
                Nhm ring t - <i>{group.totalMember} thnh vin</i>
              </p>
            </div>
            <div className="join">
              <button>
                <i>
                  <UilUserCheck />
                </i>{" "}
                 tham gia
              </button>
              <button>
                <i>
                  <UilPlus />
                </i>
                Mi{" "}
              </button>
              <button>
                <i>
                  <UilEllipsisH />
                </i>{" "}
              </button>
            </div>
            {/* <!------------------------------- Side bar ----------------------------> */}
            <div className="sidebar">
              <Link
                to={`/pageDetail/${group.groupId}`}
                className="menu-item active"
              >
                <span>
                  <i>
                    <UilHome />
                  </i>
                </span>
                <h3>Trang ch</h3>
              </Link>
              <Link to={`/pagePeoples/${group.groupId}`} className="menu-item">
                <span>
                  <i>
                    <UilUsersAlt />
                  </i>
                </span>
                <h3>Thnh vin</h3>
              </Link>
              <Link to={"/feedback"} className="menu-item">
                <span>
                  <i>
                    <UilFeedback />
                  </i>
                </span>
                <h3>Phn hi</h3>
              </Link>
              <Link to={`/pageQuizs/${group.groupId}`} className="menu-item">
                <span>
                  <i>
                    <UilFileUpload />
                  </i>
                </span>
                <h3>Bi tp (Quiz)</h3>
              </Link>
              <Link to={"/page"} className="menu-item">
                <span>
                  <i>
                    <UilSignout />
                  </i>
                </span>
                <h3>Ri nhm</h3>
              </Link>
            </div>
          </div>
          <div className="middle">
            <div className="exercise">
              <Link to={"/pageQuizsDetails"} className="profile">
                <Quiz />
              </Link>
            </div>
          </div>
          {/* <!------------------------------- End Middle ----------------------------> */}
          <div className="right">
            <div className="about">
              <div className="intro">
                <h3>Gii thiu</h3>
                <p>Cho mng cc bn  n vi lp hc.</p>
              </div>

              <div className="intro">
                <h3>Hng dn</h3>
                <p>Bc 1: Nhp vo cc bi tp bn pha tay phi.</p>
                <p>Bc 2: Chn tp mun np.</p>
                <p>Bc 3: Nhp ti tp.</p>
              </div>

              <button>Tm hiu thm</button>
            </div>
            <div className="create-quiz">
              <Link id="createQuiz" onClick={() => setShow(true)}>
                <i>
                  <UilPlus />
                </i>{" "}
                To bi tp (Quiz)
              </Link>
            </div>
          </div>
        </div>
      </main>
      <CreateModalQuiz onClose={() => setShow(false)} show={show} />
    </React.Fragment>
  );
}
