import React from "react";
import { UilPlus } from "@iconscout/react-unicons";

const AccountModal = (props) => {
  if (!props.show) {
    return null;
  }

  return (
    <div className="modal-create-account" onClick={props.onClose}>
      <div className="cart-create" onClick={(e) => e.stopPropagation()}>
        <form className="form-create">
          <h3>To ti khon</h3>

          <div className="form-input">
            <label htmlFor="avt">nh</label>
            <input type="file" id="avt" placeholder="Hy chn nh" />
          </div>

          <div className="form-input">
            <label htmlFor="mssv">M s sinh vin</label>
            <input
              type="text"
              id="mssv"
              placeholder="Hy nhp m s sinh vin"
              defaultValue="PS16509"
            />
          </div>

          <div className="form-input">
            <label htmlFor="fullname">H v tn</label>
            <input
              type="text"
              id="fullname"
              placeholder="Hy nhp h v tn"
              defaultValue="Trn Mu Phi"
            />
          </div>

          <div className="form-input">
            <label htmlFor="email">Email</label>
            <input
              type="email"
              id="email"
              placeholder="Hy nhp email"
              defaultValue="phitmps16509@fpt.edu.vn"
            />
          </div>

          <button>
            <UilPlus /> To
          </button>
        </form>
      </div>
    </div>
  );
};

export default AccountModal;
